import React from "react";
import { useEffect, useContext } from "react";
import Endpoints from "../../helpers/Endpoints";
import { LandingContext } from "./LandingState";

function Categories() {
  const lCxt = useContext(LandingContext);
  useEffect(async () => {
    await lCxt.getCategories(`/api/post-categories`);
  }, []);
  return (
    <div class="row mb-4">
      <div class="col-md-12">
        <div class="products-container">
          <div class="row">
            {Array.isArray(lCxt.state.categories) &&
              lCxt.state.categories.map((cat, key) => (
                <div
                  key={key}
                  class="col-md-6 col-lg-3 col-sm-6 mb-3 single-product"
                >
                  <img style={{
                      width:"80px",
                      height:"80px",
                      borderRadius:12
                  }} src={cat.image} alt="" class="img-fluid product-image" />
                  <h5 class="product-name">{cat.name}</h5>
                  <p class="product-details">{cat.description}</p>
                </div>
              ))}
          </div>
        </div>
      </div>
    </div>
  );
}

export default Categories;
