import React, { useState } from "react";
import { useContext } from "react";
import { useEffect } from "react";
import { LandingContext } from "./LandingState";

function Posts() {
  const landingCxt = useContext(LandingContext);
  useEffect(async () => {
    await landingCxt.getPosts(`/api/posts?start=1&limit=3`);
  }, []);
  const [slide, setslide] = useState(0);

  const paginateSlides = (snum = Number) => {
    let totalSlide = 0;
    if (Array.isArray(landingCxt.state.posts)) {
      totalSlide = landingCxt.state.posts.length;
      if (snum <= totalSlide) setslide(snum);
      if (snum > totalSlide) setslide(0);
      if (snum < 0) setslide(totalSlide);
    }
  };
  return (
    <>
      <div class="slider-menu mb-4">
        <div class="slideshow-container">
          {Array.isArray(landingCxt.state.posts) &&
            landingCxt.state.posts.map(({ post_images, ...post }, key) => (
              <div
                class="slide-in"
                key={key}
                style={{
                  display: key == slide ? "block" : "none",
                }}
              >
                <img
                  src={post_images[0]}
                  style={{
                    width: "100%",
                    height: "100%",
                    borderRadius: 8,
                  }}
                />
              </div>
            ))}

          {/* <!-- Next and previous buttons --> */}
          <a class="prev" onClick={paginateSlides.bind(null, slide - 1)}>
            &#10094;
          </a>
          <a class="next" onClick={paginateSlides.bind(null, slide - 1)}>
            &#10095;
          </a>
        </div>
        {/* <!-- The dots/circles --> */}
        <div style={{ textAlign: "center" }}>
        {Array.isArray(landingCxt.state.posts) &&
            landingCxt.state.posts.map(({ post_images, ...post }, key) => (
          <span key={key} class="dot" onclick={`currentSlide(${key})`}></span>
            ))}
        </div>
      </div>
    </>
  );
}

export default Posts;
