import React from "react";
import { Chart } from "react-google-charts";

function Regions() {
  return (
    <>
      <div class="cities-map">
        <div class="row">
          <div class="col-md-8 mb-3">
            <div id="regions_div">
              <Chart
                width="100%"
                height="100%"
                chartType="GeoChart"
                loader={<div>Loading Chart</div>}
                data={[
                  ["Country", "Members"],
                  ["Netherlands", 350000],
                  ["China", 318569],
                  ["Nigeria", 345989],
                ]}
              />
            </div>
            {/* <!-- <img src="images/map.png" alt="" class="img-fluid"> --> */}
          </div>
          <div class="col-md-4 countries-cities">
            <h5 class="mb-2">Countries</h5>
            <p class="grey-text">Nigeria</p>
            <p class="grey-text">China</p>
            <p class="grey-text mb-4">Netherlands</p>

            <h5 class="mb-2">Top Cities</h5>
            <p class="grey-text">Jos</p>
            <p class="grey-text">Kaduna</p>
            <p class="grey-text">Lagos</p>
            <p class="grey-text">Abuja</p>
            <p class="grey-text">Ibadan</p>
            <p class="grey-text">Benin</p>
            <p class="grey-text">Portharcourt</p>
            <p class="grey-text">Kano</p>
            <p class="grey-text">Adamawa</p>
          </div>
        </div>
      </div>
    </>
  );
}

export default Regions;
