import React from 'react'
import {BrowserRouter as Router, Switch, Route } from "react-router-dom"
import BuyNow from './BuyNow'
import HowToSell from './HowToSell'
import MemberShip from './MemberShip'
import Logistics from './Logistics'
import Promotions from './Promotions'
import Advertising from './Advertising'
import Faqs from './Faqs'
import { StaySafe } from './StaySafe'
import ContactUs from './ContactUs'

const StaticPage = () => {
    return (
        <Switch>
            <Route exact path="/page/how-to-sell-on-tinkoko" component={HowToSell} />
            <Route exact path="/page/buy-now" component={BuyNow} />
            <Route exact path="/page/membership"  component={MemberShip} />
            <Route exact path="/page/tinkoko-logistics"  component={Logistics} />
            <Route exact path="/page/promotions"  component={Promotions} />
            <Route exact path="/page/advertising"  component={Advertising} />
            <Route exact path="/page/faqs"  component={Faqs} />
            <Route exact path="/page/stay-safe-on-tinkoko-market"  component={StaySafe} />
            <Route exact path="/page/contact-us"  component={ContactUs} />
        </Switch>
    )
}

export default StaticPage
