const xhrequest = async (
  method = String,
  uri = String,
  config = {},
  data = {}
) => {
  try {
    return new Promise((resolve, reject) => {
      let dataform = new FormData();
      if (data) {
        for (let i = 0; i < Object.keys(data).length; i++) {
          let dprop = Object.keys(data)[i];
          let ddata = data[dprop];
          dataform.append(dprop, ddata);
        }
      }
      let xhr = new XMLHttpRequest();
      try {
        xhr.open(String(method).toUpperCase(), uri, true);
        xhr.onload = (ev) => {
          ev.currentTarget.addEventListener("error", async (err) => {
            console.log(err, "the upload onload event error");
            return reject({
              data: null,
              res: false,
              status: xhr.status,
              msg:"Error: Your request could not be loaded"
            });
          });
          let reqData = xhr.responseText;
          if (xhr.status !== 200) {
            console.log("not status 200 error ");
            return reject({
              data: xhr.responseText,
              res: false,
              status: xhr.status,
              msg:"Your request was not successful"
            });
          }
          if (xhr.status == 200 && xhr.readyState === 4) {
            return resolve({
              data:
                typeof JSON.parse(reqData) === "object"
                  ? JSON.parse(reqData)
                  : {},
              res: true,
              status: xhr.status,
            });
          } else {
            return reject({
              data: xhr.responseText,
              res: false,
              status: xhr.status,
              msg:"Something went wrong. Please refresh your page before making another request"
            });
          }
        };
        xhr.onerror = (err) => {
          return reject({
            data: xhr.responseText,
            res: false,
            status: xhr.status,
            msg:"Error: error occured, properly check request"
          });
        };
        for (let i = 0; i < Object.keys(config).length; i++) {
          let cprop = Object.keys(config)[i];
          let cdata = config[cprop];
          xhr.setRequestHeader(cprop, cdata);
        }
        xhr.send(dataform);
      } catch (error) {
        return reject({
          data: xhr.responseText,
          res: false,
          status: xhr.status,
          msg:"Error: caught an error, please check request inputs"
        });
      }
    });
  } catch (error) {
    return {
      status: false,
      msg: error,
      data: null,
      msg:"Something went wrong, could not process request. Try again"
    };
  }
};

export default xhrequest;
