import React, { createContext, useReducer } from "react";
import LandingReducer from "./LandingReducer";
import xhrequest from "../../utils/xhrequest";

export const LandingContext = createContext();
const { Provider } = LandingContext;
function LandingState(props) {
  const initialState = {
    categories: [],
    posts:[]
  };
  const [state, dispatch] = useReducer(LandingReducer, initialState);
  const setValue = (obj = {}) => {
    try {
      if (Object.keys(obj)[0]) {
        dispatch({
          type: "SET_STATE",
          payload: { ...obj },
        });
      }
    } catch (error) {}
  };

  const getCategories = async (uri) => {
    try {
      let req = await xhrequest("GET", uri, {}, {});
      if (req.data) {
        if (Array.isArray(req.data.data)) {
          setValue({
            categories: req.data.data,
          });
        }
      }
    } catch (error) {

    }
  };

  const getPosts = async (uri) => {
    try {
      let req = await xhrequest("GET", uri, {}, {});
      if (req.data) {
        if (Array.isArray(req.data.data)) {
          setValue({
            posts: req.data.data,
          });
          console.log(req.data.data, "Post data  data ")
        }
      }
    } catch (error) {

    }
  };
  return (
    <Provider value={{ state, setValue, getCategories, getPosts }}>
      {props.children}
    </Provider>
  );
}

export default LandingState;
