import React, { useEffect, useState } from "react";

const PageNav = (props) => {
  const [pageLinks, setPageLinks] = useState([
    {
      label: "How to sell on Tinkoko",
      status: "",
      href: "/page/how-to-sell-on-tinkoko",
    },
    {
      label: "Buy Now",
      status: "",
      href: "/page/buy-now",
    },
    {
      label: "Membership",
      status: "",
      href: "/page/membership",
    },
    {
      label: "Tinkoko Logistics",
      status: "",
      href: "/page/tinkoko-logistics",
    },
    {
      label: "Promotions",
      status: "",
      href: "/page/promotions",
    },
    {
      label: "Advertising",
      status: "",
      href: "/page/advertising",
    },
    {
      label: "FAQs",
      status: "",
      href: "/page/faqs",
    },
    {
      // Stay Safe on Tinkoko Market
      label: "Stay Safe on Tinkoko Market",
      status: "",
      href: "/page/stay-safe-on-tinkoko-market",
    },
    {
      label: "Contact Us",
      status: "",
      href: "/page/contact-us",
    },
  ]);

  useEffect(() => {
    if (props.activeKey) {
      let tabs = [...pageLinks];
      if (typeof props.activeKey == "number") {
        if (props.activeKey >  0 ) {
          let tabKey = props.activeKey - 1          
          tabs[tabKey] = { ...tabs[tabKey], status: "active" };
          setPageLinks([...tabs])
        }else if (props.activeKey == 0 ) {
          
        }
      }
    }
  }, []);

  return (
    <div class="sidebar-menu d-md-block d-lg-block">
      <ul>
        {pageLinks.map((tab, key) => (
          <li class={tab.status} key={key}>
            <a href={tab.href}>{tab.label}</a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default PageNav;
