import React, { useState } from "react";
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import "./css/bootstrap.css";
import "./css/jquery.popVideo.css";
import "./css/starability-growRotate.min.css";
import "./css/style.css";
import { useEffect } from "react";
import LandingPage from "./component/pages/landing/LandingPage";
import Header from "./component/common/Header";
import Footer from "./component/common/Footer";
import StaticPage from "./component/pages/page/StaticPage";
import LandingState from "./component/pages/landing/LandingState";
import NotFound from "./component/common/NotFound";

// import setAuthToken from "./utils/setAuthToken";

function App(props) {
  useEffect(() => {}, []);

  return (
    <Router>
      <Header />
      <LandingState>
        <Switch>
          <Route exact path="/" component={LandingPage} />
          <Route exact path="/page" component={StaticPage} />
          <Route exact path="/page/*" component={StaticPage} />
          <Route exact path="*" component={NotFound} />
        </Switch>
      </LandingState>
      <Footer />
    </Router>
  );
}

export default App;
