import React, { useEffect } from "react";
import { useContext } from "react";
import Categories from "./Categories";
import Posts from "./Posts";
import Regions from "./Regions";
import Services from "./Services";

const LandingPage = () => {

  return (
    <section class="default-container">
      <div class="container">
        <div class="row mb-4">
          <div class="col-md-5 mb-4">
            <Posts />
            <Services />
          </div>
          <div class="col-md-7 mb-4">
            <Regions />
          </div>
        </div>
        <div class="row mb-4">
          <div class="col-md-7 mb-4">
            <div class="be-cautious">
              <div class="media">
                <h1 class="caution-icon">
                  <i class="fa fa-shield-alt"></i>
                </h1>
                <div class="media-body">
                  <h4>Be cautious!</h4>
                  <p>
                    For authenticity, Tinkoko veriﬁes merchants, farmers using a
                    valid ID.
                  </p>
                </div>
              </div>
            </div>
          </div>
          <div class="col-md-5 mb-4">
            <div class="submit-quote text-center">
              <h5 class="bold-green-text mb-2">Submit a Quote!!</h5>
              <p class="grey-text">
                Do you want a custom request for products / services.
                <span>
                  <a
                    href="#"
                    data-toggle="modal"
                    data-target=".submit-quote-modal"
                  >
                    Click here
                  </a>
                </span>
                to submit one now.
              </p>
              <div
                class="modal fade submit-quote-modal"
                tabindex="-1"
                role="dialog"
                aria-labelledby="myLargeModalLabel"
                aria-hidden="true"
              >
                <button
                  type="button"
                  class="close"
                  data-dismiss="modal"
                  aria-label="Close"
                >
                  <span aria-hidden="true">&times;</span>
                </button>
                <div class="modal-dialog modal-lg">
                  <div class="modal-content">
                    <h4 class="bold-green-text">Submit a Quote to Tinkoko</h4>
                    <p class="grey-text">
                      Do You have a unique need? Get a custom quotation from us
                    </p>
                    <div class="quotes-form-container">
                      <form action="">
                        <div class="form-field row mb-3">
                          <label for="fullname" class="col-sm-4">
                            FULL NAMES:
                          </label>
                          <div class="col-sm-8">
                            <input type="text" id="fullname" name="fullname" />
                          </div>
                        </div>

                        <div class="form-field row mb-3">
                          <label for="quotetype" class="col-sm-4">
                            CHOOSE QUOTE TYPE:
                          </label>
                          <div class="col-sm-8">
                            <select name="quotetype" id="quotetype" required>
                              <option value="" selected>
                                Select a Quote Type
                              </option>
                            </select>
                            <i class="fa fa-chevron-down arrow-down"></i>
                          </div>
                        </div>

                        <div class="form-field row mb-3">
                          <label for="category" class="col-sm-4">
                            SELECT CATEGORY:
                          </label>
                          <div class="col-sm-8">
                            <select name="category" id="category" required>
                              <option value="" selected>
                                Select a Category
                              </option>
                            </select>
                            <i class="fa fa-chevron-down arrow-down"></i>
                          </div>
                        </div>

                        <div class="form-field row mb-3">
                          <label for="itemsneeded" class="col-sm-4">
                            ITEMS NEEDED:
                          </label>
                          <div class="col-sm-8">
                            <input
                              type="text"
                              id="itemsneeded"
                              name="itemsneeded"
                            />
                          </div>
                        </div>

                        <div class="form-field row mb-3">
                          <label for="quantity" class="col-sm-4">
                            QUANTITY:
                          </label>
                          <div class="col-sm-8">
                            <input
                              type="number"
                              id="quantity"
                              name="quantity"
                            />
                          </div>
                        </div>

                        <div class="form-field row mb-3">
                          <label for="email" class="col-sm-4">
                            EMAIL ADDRESS:
                          </label>
                          <div class="col-sm-8">
                            <input type="email" id="email" name="email" />
                          </div>
                        </div>

                        <div class="form-field row mb-3">
                          <label for="phonenumber" class="col-sm-4">
                            PHONE NUMBER:
                          </label>
                          <div class="col-sm-8">
                            <input
                              type="number"
                              id="phonenumber"
                              name="phonenumber"
                            />
                          </div>
                        </div>
                        <button type="submit">Submit Quote</button>
                      </form>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <Categories />

        <div class="row">
          <div class="col-md-12 do-you-have-a-farm">
            <h5>Do you have a farm, an authentic agro product for sale</h5>
            <p>
              Tinkoko Market place is the best place for selling fast and
              easily…
            </p>
            <a href="/seller" class="sell-now-btn">
              Sell them now!
            </a>
          </div>
        </div>
      </div>
    </section>
  );
};

export default LandingPage;
